import { useEffect, useState ,useLayoutEffect} from 'react';
import { Link , useLocation, useParams } from 'react-router-dom';

const PopPr = () => {
	function menuPopClose(){// 팝업 닫기
		document.querySelector("body").classList.remove("pop_on");
		const _pw = document.querySelectorAll("[class*='_pop_wrap']");
		Array.from(_pw).forEach(function(v){
			//console.log(v);
			v.classList.remove("on");
			document.querySelector(".pr_pop_wrap iframe").setAttribute("src","");
		});
	};

	return (
		<div className="pr_pop_wrap">
			<div className="pop_inwrap">
				<div className="head">
					<h2 className="tit">{document.querySelector("html").matches("[lang='en']")?'PR VIDEO':'홍보영상'}</h2>
				</div>
				<div className="body">
					<iframe width="856" height="482" src="" title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
					{/*<video controls autoPlay name="media">
						<source src="http://www.kosteel.co.kr/img/pr_movie.mp4" type="video/mp4" />
					</video>*/}
				</div>
				<button type="button" className="ibtn_close" onClick={menuPopClose}><span className="blind">닫기</span></button>
			</div>
		</div>
	)
}

export default PopPr;